/* ================================
   019 SOLUTIONS - MODERN TECH STYLES
   INSPIRED BY VIBRANT NEON COLORS
   ================================ */

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&family=JetBrains+Mono:wght@400;500;600;700&display=swap');

:root {
  /* Vibrant Tech Colors - Inspired by provided image */
  --primary-bg: #0a0a0f;
  --secondary-bg: #1a1a2e;
  --accent-pink: #ff0080;
  --accent-blue: #00d4ff;
  --accent-purple: #8a2be2;
  --accent-cyan: #00ffff;
  --accent-orange: #ff6b35;
  
  /* Modern Gradients */
  --gradient-primary: linear-gradient(135deg, #ff0080, #8a2be2, #00d4ff);
  --gradient-secondary: linear-gradient(135deg, #00ffff, #8a2be2, #ff0080);
  --gradient-hero: linear-gradient(135deg, #0a0a0f 0%, #1a1a2e 50%, #16213e 100%);
  --gradient-card: linear-gradient(135deg, rgba(255,0,128,0.1), rgba(138,43,226,0.1), rgba(0,212,255,0.1));
  
  /* Text Colors */
  --text-primary: #ffffff;
  --text-secondary: rgba(255, 255, 255, 0.8);
  --text-muted: rgba(255, 255, 255, 0.6);
  --text-accent: #00d4ff;
  
  /* UI Elements */
  --border-primary: rgba(255, 255, 255, 0.1);
  --border-accent: rgba(0, 212, 255, 0.3);
  --shadow-glow: 0 0 30px rgba(0, 212, 255, 0.3);
  --shadow-pink: 0 0 30px rgba(255, 0, 128, 0.3);
  --shadow-purple: 0 0 30px rgba(138, 43, 226, 0.3);
  
  /* Typography */
  --font-primary: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  --font-mono: 'JetBrains Mono', monospace;
  
  /* Spacing & Layout */
  --container-max: 1200px;
  --section-padding: 6rem 0;
  --nav-height: 80px;
  --border-radius: 12px;
  --card-radius: 20px;
  
  /* Animations */
  --transition-fast: 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-normal: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-slow: 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

/* ================================
   GLOBAL RESET & BASE STYLES
   ================================ */

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  font-family: var(--font-primary);
  font-weight: 400;
  line-height: 1.6;
  color: var(--text-primary);
  background: var(--primary-bg);
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* ================================
   MODERN APP CONTAINER
   ================================ */

.modern-app {
  min-height: 100vh;
  background: var(--primary-bg);
  position: relative;
}

/* ================================
   MODERN NAVIGATION
   ================================ */

.modern-nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: var(--nav-height);
  z-index: 1000;
  background: rgba(10, 10, 15, 0.95);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid var(--border-primary);
  transition: var(--transition-normal);
}

.nav-container {
  max-width: var(--container-max);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  height: 100%;
}

.nav-brand {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.brand-logo {
  font-size: 2rem;
  font-weight: 800;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-family: var(--font-mono);
}

.brand-text {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
  font-family: var(--font-mono);
  letter-spacing: 0.1em;
}

.burger-container {
  position: relative;
}

.burger-menu {
  width: 50px;
  height: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: transparent;
  border: 2px solid var(--border-primary);
  border-radius: var(--border-radius);
  padding: 10px;
  transition: var(--transition-normal);
  position: relative;
  z-index: 1001;
}

.burger-menu:hover,
.burger-menu.active {
  border-color: var(--accent-blue);
  background: rgba(0, 212, 255, 0.1);
  box-shadow: var(--shadow-glow);
}

.burger-line {
  width: 24px;
  height: 3px;
  background: var(--text-primary);
  margin: 3px 0;
  transition: var(--transition-fast);
  border-radius: 2px;
}

.burger-menu:hover .burger-line,
.burger-menu.active .burger-line {
  background: var(--accent-blue);
}

.burger-menu.active .burger-line:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
}

.burger-menu.active .burger-line:nth-child(2) {
  opacity: 0;
}

.burger-menu.active .burger-line:nth-child(3) {
  transform: rotate(-45deg) translate(8px, -8px);
}

.nav-menu {
  position: fixed;
  top: var(--nav-height);
  right: -100%;
  width: min(400px, 90vw);
  height: calc(100vh - var(--nav-height));
  background: rgba(10, 10, 15, 0.98);
  backdrop-filter: blur(30px);
  border-left: 1px solid var(--border-accent);
  transition: var(--transition-normal);
  display: flex;
  flex-direction: column;
  z-index: 1000;
  box-shadow: var(--shadow-glow);
}

.nav-menu.active {
  right: 0;
}

.menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  border-bottom: 1px solid var(--border-primary);
  background: var(--gradient-card);
}

.menu-header h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
  font-family: var(--font-mono);
}

.menu-items {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  color: var(--text-secondary);
  padding: 1.2rem 1.5rem;
  margin-bottom: 0.8rem;
  border-radius: var(--border-radius);
  transition: var(--transition-normal);
  background: transparent;
  border: 2px solid transparent;
  width: 100%;
  text-align: left;
  cursor: pointer;
  font-family: var(--font-primary);
  font-size: 1rem;
  font-weight: 500;
  position: relative;
  overflow: hidden;
}

.nav-item::before {
  content: '';
  position: absolute;
  left: -100%;
  top: 0;
  width: 100%;
  height: 100%;
  background: var(--gradient-primary);
  transition: var(--transition-normal);
  z-index: -1;
  opacity: 0.1;
}

.nav-item:hover::before {
  left: 0;
}

.nav-item:hover {
  color: var(--text-primary);
  border-color: var(--accent-blue);
  transform: translateX(10px);
  box-shadow: var(--shadow-glow);
}

.nav-icon {
  font-size: 1.4rem;
  transition: var(--transition-fast);
}

.nav-item:hover .nav-icon {
  transform: scale(1.2);
}

.nav-label {
  font-weight: 500;
  letter-spacing: 0.02em;
}

/* ================================
   MODERN HERO SECTION
   ================================ */

.modern-hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gradient-hero);
  position: relative;
  overflow: hidden;
  padding-top: var(--nav-height);
}

.hero-bg-gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(ellipse at center, rgba(255,0,128,0.1) 0%, rgba(138,43,226,0.1) 50%, rgba(0,212,255,0.1) 100%);
  animation: pulse-gradient 4s ease-in-out infinite alternate;
}

@keyframes pulse-gradient {
  0% { opacity: 0.3; }
  100% { opacity: 0.6; }
}

.hero-content {
  position: relative;
  z-index: 10;
  text-align: center;
  max-width: 1100px;
  padding: 3rem 2rem;
}

.hero-logo {
  margin-bottom: 3rem;
}

.logo-main {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.logo-number {
  font-size: clamp(4rem, 8vw, 6rem);
  font-weight: 900;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-family: var(--font-mono);
  text-shadow: 0 0 40px rgba(0, 212, 255, 0.5);
}

.logo-text {
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: 700;
  color: var(--text-primary);
  font-family: var(--font-mono);
  letter-spacing: 0.15em;
}

.hero-tagline {
  margin-bottom: 3rem;
}

.hero-title {
  font-size: clamp(2rem, 5vw, 3.5rem);
  font-weight: 300;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  line-height: 1.2;
  font-family: var(--font-primary);
}

.cursor {
  animation: blink 1s infinite;
  color: var(--accent-blue);
  font-weight: 400;
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

.hero-subtitle {
  font-size: clamp(1.1rem, 2vw, 1.4rem);
  color: var(--text-secondary);
  font-weight: 400;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: 3rem;
  margin: 3rem 0;
  flex-wrap: wrap;
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  padding: 2rem 1.5rem;
  background: var(--gradient-card);
  border: 2px solid var(--border-primary);
  border-radius: var(--card-radius);
  transition: var(--transition-normal);
  backdrop-filter: blur(10px);
  min-width: 150px;
}

.stat-item:hover {
  border-color: var(--accent-blue);
  transform: translateY(-10px);
  box-shadow: var(--shadow-glow);
}

.stat-icon {
  font-size: 2.5rem;
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: 600;
  font-family: var(--font-mono);
}

.hero-actions {
  display: flex;
  gap: 2rem;
  justify-content: center;
  margin-top: 3rem;
  flex-wrap: wrap;
}

.btn-primary,
.btn-secondary {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 1.2rem 2.5rem;
  border: none;
  border-radius: var(--border-radius);
  font-family: var(--font-primary);
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition-normal);
  text-decoration: none;
  position: relative;
  overflow: hidden;
  min-height: 56px;
}

.btn-primary {
  background: var(--gradient-primary);
  color: var(--text-primary);
  box-shadow: var(--shadow-pink);
}

.btn-secondary {
  background: transparent;
  color: var(--text-primary);
  border: 2px solid var(--accent-blue);
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 40px rgba(255, 0, 128, 0.4);
}

.btn-secondary:hover {
  background: rgba(0, 212, 255, 0.1);
  transform: translateY(-3px);
  box-shadow: var(--shadow-glow);
}

.btn-icon {
  font-size: 1.2rem;
}

/* ================================
   MODERN SERVICES SECTION
   ================================ */

.modern-services {
  padding: var(--section-padding);
  background: var(--secondary-bg);
  position: relative;
}

.container {
  max-width: var(--container-max);
  margin: 0 auto;
  padding: 0 2rem;
}

.section-header {
  text-align: center;
  margin-bottom: 4rem;
}

.section-title {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 700;
  margin-bottom: 1.5rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.02em;
  line-height: 1.1;
  font-family: var(--font-primary);
}

.section-subtitle {
  font-size: 1.2rem;
  color: var(--text-secondary);
  font-weight: 400;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2.5rem;
  margin-top: 4rem;
}

.service-card {
  background: var(--gradient-card);
  border: 2px solid var(--border-primary);
  border-radius: var(--card-radius);
  padding: 2.5rem;
  text-align: center;
  transition: var(--transition-normal);
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(20px);
}

.service-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0,212,255,0.1), transparent);
  transition: var(--transition-slow);
}

.service-card:hover::before {
  left: 100%;
}

.service-card:hover {
  transform: translateY(-10px);
  border-color: var(--accent-blue);
  box-shadow: var(--shadow-glow);
}

.service-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.service-icon {
  font-size: 3.5rem;
  transition: var(--transition-normal);
}

.service-card:hover .service-icon {
  transform: scale(1.1) rotate(5deg);
}

.service-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
  font-family: var(--font-primary);
}

.service-description {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 2rem;
  font-size: 1rem;
}

.service-features {
  list-style: none;
  margin-bottom: 2.5rem;
  text-align: left;
}

.service-features li {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.6rem 0;
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.feature-icon {
  color: var(--accent-blue);
  font-weight: 700;
  font-size: 1.1rem;
  width: 20px;
  text-align: center;
}

.service-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  width: 100%;
  padding: 1rem;
  background: var(--gradient-secondary);
  color: var(--text-primary);
  border: none;
  border-radius: var(--border-radius);
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition-normal);
  font-family: var(--font-primary);
}

.service-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(138, 43, 226, 0.4);
}

/* ================================
   MODERN PORTFOLIO SECTION
   ================================ */

.modern-portfolio {
  padding: var(--section-padding);
  background: var(--primary-bg);
  position: relative;
}

.portfolio-filters {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.filter-btn {
  background: transparent;
  border: 2px solid var(--border-primary);
  color: var(--text-secondary);
  padding: 0.8rem 2rem;
  border-radius: 25px;
  cursor: pointer;
  transition: var(--transition-normal);
  font-family: var(--font-primary);
  font-weight: 500;
  font-size: 0.95rem;
  position: relative;
  overflow: hidden;
}

.filter-btn::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: var(--gradient-primary);
  border-radius: 50%;
  transition: var(--transition-normal);
  transform: translate(-50%, -50%);
  z-index: -1;
}

.filter-btn:hover::before,
.filter-btn.active::before {
  width: 200%;
  height: 200%;
}

.filter-btn:hover,
.filter-btn.active {
  color: var(--text-primary);
  border-color: var(--accent-blue);
  transform: translateY(-2px);
  box-shadow: var(--shadow-glow);
}

.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2.5rem;
  margin-top: 4rem;
}

.portfolio-card {
  background: var(--gradient-card);
  border: 2px solid var(--border-primary);
  border-radius: var(--card-radius);
  overflow: hidden;
  transition: var(--transition-normal);
  position: relative;
  backdrop-filter: blur(20px);
}

.portfolio-card:hover {
  transform: translateY(-10px);
  border-color: var(--accent-purple);
  box-shadow: var(--shadow-purple);
}

.project-image {
  position: relative;
  overflow: hidden;
  aspect-ratio: 16/9;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition-slow);
}

.portfolio-card:hover .project-image img {
  transform: scale(1.1);
}

.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 10, 15, 0.8);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  opacity: 0;
  transition: var(--transition-normal);
}

.portfolio-card:hover .project-overlay {
  opacity: 1;
}

.project-status {
  background: var(--gradient-primary);
  color: var(--text-primary);
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-family: var(--font-mono);
}

.project-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--gradient-secondary);
  color: var(--text-primary);
  padding: 0.8rem 1.5rem;
  border-radius: var(--border-radius);
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  transition: var(--transition-normal);
}

.project-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 255, 255, 0.3);
}

.link-icon {
  font-size: 1.1rem;
}

.project-content {
  padding: 2rem;
}

.project-category {
  display: inline-block;
  background: var(--gradient-primary);
  color: var(--text-primary);
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-family: var(--font-mono);
}

.project-title {
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: var(--text-primary);
  font-family: var(--font-primary);
}

.project-description {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: rgba(0, 212, 255, 0.1);
  color: var(--accent-blue);
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid rgba(0, 212, 255, 0.2);
  transition: var(--transition-fast);
  font-family: var(--font-mono);
}

.tech-tag:hover {
  background: rgba(0, 212, 255, 0.2);
  border-color: var(--accent-blue);
}

/* ================================
   MODERN CONTACT SECTION
   ================================ */

.modern-contact {
  padding: var(--section-padding);
  background: var(--secondary-bg);
  position: relative;
}

.contact-layout {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: start;
  margin-top: 4rem;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.contact-item {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  padding: 2rem;
  background: var(--gradient-card);
  border: 2px solid var(--border-primary);
  border-radius: var(--card-radius);
  transition: var(--transition-normal);
  backdrop-filter: blur(20px);
}

.contact-item:hover {
  border-color: var(--accent-blue);
  transform: translateX(10px);
  box-shadow: var(--shadow-glow);
}

.contact-icon {
  font-size: 2rem;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gradient-primary);
  border-radius: 15px;
  flex-shrink: 0;
  transition: var(--transition-normal);
}

.contact-item:hover .contact-icon {
  transform: scale(1.1) rotate(5deg);
}

.contact-details h4 {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0.8rem;
  color: var(--text-primary);
  font-family: var(--font-primary);
}

.contact-details a {
  color: var(--accent-blue);
  text-decoration: none;
  font-weight: 500;
  transition: var(--transition-fast);
}

.contact-details a:hover {
  color: var(--accent-cyan);
  text-decoration: underline;
}

.contact-form {
  background: var(--gradient-card);
  border: 2px solid var(--border-primary);
  padding: 3rem;
  border-radius: var(--card-radius);
  backdrop-filter: blur(20px);
  transition: var(--transition-normal);
}

.contact-form:hover {
  border-color: var(--accent-blue);
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.form-input,
.form-textarea {
  width: 100%;
  padding: 1.2rem;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid var(--border-primary);
  border-radius: var(--border-radius);
  color: var(--text-primary);
  font-size: 1rem;
  font-family: var(--font-primary);
  font-weight: 400;
  transition: var(--transition-normal);
}

.form-input:focus,
.form-textarea:focus {
  outline: none;
  border-color: var(--accent-blue);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(0, 212, 255, 0.1);
}

.form-input::placeholder,
.form-textarea::placeholder {
  color: var(--text-muted);
  font-family: var(--font-primary);
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
  grid-column: 1 / -1;
  margin-bottom: 1.5rem;
}

.form-submit {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  width: 100%;
  padding: 1.3rem;
  background: var(--gradient-secondary);
  color: var(--text-primary);
  border: none;
  border-radius: var(--border-radius);
  font-family: var(--font-primary);
  font-weight: 700;
  font-size: 1.1rem;
  cursor: pointer;
  transition: var(--transition-normal);
}

.form-submit:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(0, 255, 255, 0.4);
}

.form-submit:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.submit-message {
  margin-top: 1.5rem;
  padding: 1.2rem;
  border-radius: var(--border-radius);
  text-align: center;
  font-weight: 600;
  background: rgba(0, 255, 0, 0.1);
  color: #00ff00;
  border: 2px solid rgba(0, 255, 0, 0.2);
}

/* ================================
   MODERN FOOTER
   ================================ */

.modern-footer {
  background: var(--primary-bg);
  border-top: 2px solid var(--border-primary);
  padding: 4rem 0 2rem;
}

.footer-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 3rem;
  margin-bottom: 3rem;
}

.footer-brand {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.footer-logo .logo-number {
  font-size: 2rem;
  font-weight: 800;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-family: var(--font-mono);
}

.footer-logo .logo-text {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
  font-family: var(--font-mono);
  letter-spacing: 0.1em;
}

.footer-brand p {
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 0.95rem;
  margin-bottom: 0.5rem;
}

.footer-column h4 {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  font-family: var(--font-primary);
}

.footer-column ul {
  list-style: none;
}

.footer-column ul li {
  margin-bottom: 0.8rem;
}

.footer-column ul li a {
  color: var(--text-secondary);
  transition: var(--transition-fast);
  font-size: 0.95rem;
  position: relative;
  text-decoration: none;
}

.footer-column ul li a:hover {
  color: var(--accent-blue);
  padding-left: 1rem;
}

.footer-column ul li a:hover::before {
  content: '';
  position: absolute;
  left: 0;
  color: var(--accent-blue);
}

.footer-bottom {
  border-top: 1px solid var(--border-primary);
  padding-top: 2rem;
  text-align: center;
}

.footer-bottom p {
  color: var(--text-muted);
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

/* ================================
   RESPONSIVE DESIGN
   ================================ */

@media (max-width: 768px) {
  .nav-menu {
    width: 100vw;
  }
  
  .hero-stats {
    flex-direction: column;
    gap: 1rem;
  }
  
  .hero-actions {
    flex-direction: column;
    align-items: center;
  }
  
  .services-grid,
  .portfolio-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .contact-layout {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .form-row {
    grid-template-columns: 1fr;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .section-title {
    font-size: 2.5rem;
  }
  
  .portfolio-filters {
    flex-direction: column;
    align-items: center;
  }
  
  .burger-menu {
    width: 45px;
    height: 45px;
  }
  
  .logo-main {
    flex-direction: column;
    gap: 0.5rem;
  }
}

/* ================================
   UTILITY CLASSES
   ================================ */

.container {
  max-width: var(--container-max);
  margin: 0 auto;
  padding: 0 2rem;
}

/* ================================
   SCROLL BAR STYLING
   ================================ */

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--primary-bg);
}

::-webkit-scrollbar-thumb {
  background: var(--gradient-primary);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--gradient-secondary);
}

/* ================================
   SELECTION STYLING
   ================================ */

::selection {
  background: var(--accent-blue);
  color: var(--primary-bg);
}

/* ================================
   FOCUS STATES
   ================================ */

button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: 2px solid var(--accent-blue);
  outline-offset: 2px;
}

/* ================================
   ANIMATIONS
   ================================ */

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in-up {
  animation: fadeInUp 0.6s ease-out;
}

/* ================================
   ADMIN PANEL INTEGRATION
   ================================ */

.modern-admin-section {
  padding: var(--section-padding);
  background: var(--secondary-bg);
  min-height: 100vh;
}

.semantic-search-section {
  padding: var(--section-padding);
  background: var(--primary-bg);
}

/* ================================
   MODERN TECH ICONS
   ================================ */

.service-icon-container {
  position: relative;
  width: 80px;
  height: 80px;
  margin-bottom: 1.5rem;
  transition: var(--transition-normal);
}

.modern-tech-icon {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  background: var(--gradient-card);
  border: 2px solid var(--border-primary);
  backdrop-filter: blur(20px);
  transition: var(--transition-normal);
  z-index: 2;
  position: relative;
}

.legacy-icon {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gradient-primary);
  border-radius: 50%;
  font-size: 1.2rem;
  z-index: 3;
  border: 2px solid var(--primary-bg);
}

.service-icon-container:hover .modern-tech-icon {
  transform: scale(1.1) rotate(5deg);
  box-shadow: var(--shadow-glow);
}

.service-icon-container:hover .legacy-icon {
  transform: scale(1.2) rotate(-10deg);
}

/* Full-Stack Icon */
.tech-icon.full-stack .icon-stack {
  position: relative;
  width: 40px;
  height: 40px;
}

.tech-icon.full-stack .layer {
  position: absolute;
  border-radius: 6px;
  width: 40px;
  height: 8px;
}

.tech-icon.full-stack .frontend {
  top: 0;
  background: linear-gradient(135deg, #00d4ff, #0099cc);
  animation: stackPulse 2s ease-in-out infinite;
}

.tech-icon.full-stack .backend {
  top: 12px;
  background: linear-gradient(135deg, #ff0080, #cc0066);
  animation: stackPulse 2s ease-in-out infinite 0.3s;
}

.tech-icon.full-stack .database {
  top: 24px;
  background: linear-gradient(135deg, #8a2be2, #6a1b9a);
  animation: stackPulse 2s ease-in-out infinite 0.6s;
}

@keyframes stackPulse {
  0%, 100% { opacity: 0.7; transform: scaleX(1); }
  50% { opacity: 1; transform: scaleX(1.1); }
}

/* Responsive Icon */
.tech-icon.responsive .device-stack {
  position: relative;
  width: 40px;
  height: 40px;
}

.tech-icon.responsive .desktop {
  width: 40px;
  height: 24px;
  background: linear-gradient(135deg, #00d4ff, #0099cc);
  border-radius: 4px;
  position: absolute;
  top: 0;
  animation: deviceFloat 3s ease-in-out infinite;
}

.tech-icon.responsive .tablet {
  width: 30px;
  height: 20px;
  background: linear-gradient(135deg, #ff0080, #cc0066);
  border-radius: 3px;
  position: absolute;
  top: 8px;
  left: 5px;
  animation: deviceFloat 3s ease-in-out infinite 0.5s;
}

.tech-icon.responsive .mobile {
  width: 20px;
  height: 16px;
  background: linear-gradient(135deg, #8a2be2, #6a1b9a);
  border-radius: 2px;
  position: absolute;
  top: 16px;
  left: 10px;
  animation: deviceFloat 3s ease-in-out infinite 1s;
}

@keyframes deviceFloat {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-3px); }
}

/* E-commerce Icon */
.tech-icon.ecommerce .cart-container {
  position: relative;
  width: 40px;
  height: 40px;
}

.tech-icon.ecommerce .cart-base {
  width: 30px;
  height: 20px;
  background: linear-gradient(135deg, #00d4ff, #0099cc);
  border-radius: 4px;
  position: absolute;
  top: 10px;
  left: 5px;
}

.tech-icon.ecommerce .cart-wheels::before,
.tech-icon.ecommerce .cart-wheels::after {
  content: '';
  position: absolute;
  width: 6px;
  height: 6px;
  background: var(--accent-pink);
  border-radius: 50%;
  bottom: 2px;
}

.tech-icon.ecommerce .cart-wheels::before {
  left: 8px;
}

.tech-icon.ecommerce .cart-wheels::after {
  right: 8px;
}

.tech-icon.ecommerce .cart-handle {
  width: 12px;
  height: 12px;
  border: 3px solid var(--accent-blue);
  border-radius: 50%;
  position: absolute;
  top: 5px;
  right: 2px;
  border-bottom: none;
}

/* Performance Icon */
.tech-icon.performance {
  position: relative;
}

.tech-icon.performance .speed-lines {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.tech-icon.performance .line {
  height: 2px;
  background: var(--gradient-primary);
  margin: 4px 0;
  border-radius: 1px;
  animation: speedLines 1.5s ease-in-out infinite;
}

.tech-icon.performance .line-1 {
  width: 20px;
  animation-delay: 0s;
}

.tech-icon.performance .line-2 {
  width: 25px;
  animation-delay: 0.2s;
}

.tech-icon.performance .line-3 {
  width: 15px;
  animation-delay: 0.4s;
}

.tech-icon.performance .rocket {
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 20px solid var(--accent-blue);
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  animation: rocketBoost 2s ease-in-out infinite;
}

@keyframes speedLines {
  0%, 100% { opacity: 0.3; transform: scaleX(0.8); }
  50% { opacity: 1; transform: scaleX(1.2); }
}

@keyframes rocketBoost {
  0%, 100% { transform: translateY(-50%) rotate(45deg) scale(1); }
  50% { transform: translateY(-50%) rotate(45deg) scale(1.2); }
}

/* Hardware Icon */
.tech-icon.hardware .cpu-container {
  position: relative;
  width: 40px;
  height: 40px;
}

.tech-icon.hardware .cpu-core {
  width: 30px;
  height: 30px;
  background: linear-gradient(135deg, #00d4ff, #0099cc);
  border: 2px solid var(--accent-pink);
  border-radius: 4px;
  position: absolute;
  top: 5px;
  left: 5px;
}

.tech-icon.hardware .cpu-pins::before,
.tech-icon.hardware .cpu-pins::after {
  content: '';
  position: absolute;
  background: var(--accent-purple);
}

.tech-icon.hardware .cpu-pins::before {
  width: 40px;
  height: 2px;
  top: 19px;
  left: 0;
}

.tech-icon.hardware .cpu-pins::after {
  width: 2px;
  height: 40px;
  top: 0;
  left: 19px;
}

.tech-icon.hardware .heat-waves {
  position: absolute;
  top: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 10px;
  opacity: 0.7;
}

.tech-icon.hardware .heat-waves::before,
.tech-icon.hardware .heat-waves::after {
  content: '';
  position: absolute;
  width: 6px;
  height: 6px;
  background: var(--accent-orange);
  border-radius: 50%;
  animation: heatWave 2s ease-in-out infinite;
}

.tech-icon.hardware .heat-waves::before {
  left: 2px;
}

.tech-icon.hardware .heat-waves::after {
  right: 2px;
  animation-delay: 0.5s;
}

@keyframes heatWave {
  0%, 100% { opacity: 0.3; transform: translateY(0); }
  50% { opacity: 1; transform: translateY(-5px); }
}

/* PHP/Laravel Icon */
.tech-icon.php-laravel .php-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
  font-family: var(--font-mono);
  font-weight: 700;
}

.tech-icon.php-laravel .php-bracket {
  font-size: 1.2rem;
  color: var(--accent-blue);
  animation: bracketPulse 2s ease-in-out infinite;
}

.tech-icon.php-laravel .php-text {
  font-size: 1rem;
  color: var(--accent-pink);
  animation: textGlow 2s ease-in-out infinite;
}

.tech-icon.php-laravel .open {
  animation-delay: 0s;
}

.tech-icon.php-laravel .close {
  animation-delay: 1s;
}

@keyframes bracketPulse {
  0%, 100% { transform: scale(1); color: var(--accent-blue); }
  50% { transform: scale(1.2); color: var(--accent-purple); }
}

@keyframes textGlow {
  0%, 100% { color: var(--accent-pink); text-shadow: none; }
  50% { color: var(--accent-cyan); text-shadow: 0 0 10px var(--accent-cyan); }
}

/* Gaming Icon */
.tech-icon.gaming .controller-container {
  position: relative;
  width: 40px;
  height: 40px;
}

.tech-icon.gaming .controller-body {
  width: 35px;
  height: 20px;
  background: linear-gradient(135deg, #00d4ff, #0099cc);
  border-radius: 10px;
  position: absolute;
  top: 10px;
  left: 2.5px;
}

.tech-icon.gaming .d-pad {
  width: 8px;
  height: 8px;
  background: var(--accent-pink);
  position: absolute;
  top: 16px;
  left: 8px;
}

.tech-icon.gaming .d-pad::before,
.tech-icon.gaming .d-pad::after {
  content: '';
  position: absolute;
  background: var(--accent-pink);
}

.tech-icon.gaming .d-pad::before {
  width: 2px;
  height: 8px;
  left: 3px;
  top: 0;
}

.tech-icon.gaming .d-pad::after {
  width: 8px;
  height: 2px;
  left: 0;
  top: 3px;
}

.tech-icon.gaming .buttons::before,
.tech-icon.gaming .buttons::after {
  content: '';
  position: absolute;
  width: 4px;
  height: 4px;
  background: var(--accent-purple);
  border-radius: 50%;
  animation: buttonPress 1.5s ease-in-out infinite;
}

.tech-icon.gaming .buttons::before {
  top: 14px;
  right: 8px;
}

.tech-icon.gaming .buttons::after {
  top: 18px;
  right: 12px;
  animation-delay: 0.5s;
}

@keyframes buttonPress {
  0%, 100% { transform: scale(1); opacity: 0.7; }
  50% { transform: scale(1.3); opacity: 1; }
}

/* AI Integration Icon */
.tech-icon.ai-integration .ai-brain {
  position: relative;
  width: 40px;
  height: 40px;
}

.tech-icon.ai-integration .neural-network {
  position: relative;
  width: 100%;
  height: 100%;
}

.tech-icon.ai-integration .node {
  position: absolute;
  width: 8px;
  height: 8px;
  background: var(--gradient-primary);
  border-radius: 50%;
  animation: neuralPulse 2s ease-in-out infinite;
}

.tech-icon.ai-integration .node-1 {
  top: 5px;
  left: 16px;
  animation-delay: 0s;
}

.tech-icon.ai-integration .node-2 {
  top: 16px;
  left: 5px;
  animation-delay: 0.3s;
}

.tech-icon.ai-integration .node-3 {
  top: 16px;
  right: 5px;
  animation-delay: 0.6s;
}

.tech-icon.ai-integration .connections::before,
.tech-icon.ai-integration .connections::after {
  content: '';
  position: absolute;
  height: 1px;
  background: var(--gradient-secondary);
  animation: connectionFlow 2s ease-in-out infinite;
}

.tech-icon.ai-integration .connections::before {
  width: 15px;
  top: 9px;
  left: 20px;
  transform: rotate(30deg);
}

.tech-icon.ai-integration .connections::after {
  width: 15px;
  top: 9px;
  right: 20px;
  transform: rotate(-30deg);
  animation-delay: 0.5s;
}

@keyframes neuralPulse {
  0%, 100% { transform: scale(1); opacity: 0.7; }
  50% { transform: scale(1.5); opacity: 1; }
}

@keyframes connectionFlow {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 1; }
}

/* Matrix Canvas Background */
.matrix-background {
  pointer-events: none;
}

