}

.stat-card {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid var(--border-primary);
  border-radius: var(--card-radius);
  backdrop-filter: blur(20px);
  transition: var(--transition-normal);
}

.stat-card:hover {
  border-color: var(--accent-blue);
  transform: translateY(-5px);
  box-shadow: var(--shadow-glow);
}

.stat-icon {
  font-size: 3rem;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gradient-primary);
  border-radius: 20px;
}

.stat-info h3 {
  font-size: 2.5rem;
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  font-family: var(--font-mono);
}

.stat-info p {
  color: var(--text-secondary);
  font-size: 1rem;
  font-family: var(--font-primary);
  font-weight: 500;
}

.recent-activity h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  font-family: var(--font-primary);
}

.activity-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.activity-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid var(--border-primary);
  border-radius: var(--border-radius);
  backdrop-filter: blur(10px);
  transition: var(--transition-normal);
}

.activity-item:hover {
  border-color: var(--accent-blue);
  transform: translateX(10px);
}

.activity-icon {
  font-size: 1.5rem;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gradient-secondary);
  border-radius: 12px;
}

.activity-content p {
  color: var(--text-primary);
  font-weight: 600;
  margin-bottom: 0.3rem;
  font-family: var(--font-primary);
}

.activity-content small {
  color: var(--text-muted);
  font-size: 0.85rem;
  font-family: var(--font-primary);
}

.content-management,
.analytics-dashboard,
.admin-settings {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.content-section h3,
.analytics-card h3,
.settings-card h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  font-family: var(--font-primary);
}

.content-grid,
.analytics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.content-card,
.analytics-card,
.settings-card {
  padding: 2rem;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid var(--border-primary);
  border-radius: var(--card-radius);
  backdrop-filter: blur(20px);
  transition: var(--transition-normal);
}

.content-card:hover,
.analytics-card:hover {
  border-color: var(--accent-blue);
  transform: translateY(-5px);
  box-shadow: var(--shadow-glow);
}

.content-card h4 {
  font-size: 1.3rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-family: var(--font-primary);
}

.content-card p {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  font-family: var(--font-primary);
}

.content-actions {
  display: flex;
  gap: 1rem;
}

.btn-secondary {
  padding: 0.8rem 1.5rem;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid var(--border-primary);
  border-radius: var(--border-radius);
  color: var(--text-primary);
  font-family: var(--font-primary);
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition-normal);
}

.btn-secondary:hover {
  border-color: var(--accent-blue);
  background: rgba(0, 212, 255, 0.1);
}

.btn-primary {
  padding: 0.8rem 1.5rem;
  background: var(--gradient-primary);
  border: none;
  border-radius: var(--border-radius);
  color: var(--text-primary);
  font-family: var(--font-primary);
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition-normal);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-glow);
}

.chart-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  color: var(--text-muted);
  font-size: 1.1rem;
  font-family: var(--font-primary);
  border: 2px dashed var(--border-primary);
  border-radius: var(--border-radius);
}

.setting-item {
  margin-bottom: 1.5rem;
}

.setting-item label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
  font-weight: 600;
  font-family: var(--font-primary);
}

.setting-item input[type="checkbox"] {
  margin-right: 0.5rem;
  transform: scale(1.2);
}

/* ================================
   MODERN PAYMENT ICONS - REAL DESIGNS
   ================================ */

.modern-payment-icons {
  margin-top: 2rem;
  padding: 2rem;
  background: var(--gradient-card);
  border: 2px solid var(--border-primary);
  border-radius: var(--card-radius);
  backdrop-filter: blur(20px);
}

.payment-title {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  text-align: center;
  font-family: var(--font-primary);
}

.payment-icons-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  align-items: center;
}

@media (max-width: 768px) {
  .payment-icons-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

.payment-icon-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  padding: 1rem;
  border-radius: var(--border-radius);
  transition: var(--transition-normal);
  cursor: pointer;
}

.payment-icon-card:hover {
  transform: translateY(-5px);
  background: rgba(255, 255, 255, 0.05);
}

.payment-icon {
  width: 60px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  transition: var(--transition-normal);
  position: relative;
  overflow: hidden;
}

/* VISA CARD DESIGN */
.payment-icon.visa {
  background: linear-gradient(135deg, #1a1f71, #0052cc);
  box-shadow: 0 4px 15px rgba(0, 82, 204, 0.3);
}

.visa-logo {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.visa-text {
  font-family: var(--font-mono);
  font-weight: 700;
  font-size: 12px;
  color: white;
  letter-spacing: 2px;
}

.visa-chip {
  position: absolute;
  top: 5px;
  left: 8px;
  width: 12px;
  height: 10px;
  background: linear-gradient(135deg, #ffd700, #ffed4e);
  border-radius: 2px;
  border: 1px solid #e6c200;
}

/* MASTERCARD DESIGN */
.payment-icon.mastercard {
  background: linear-gradient(135deg, #eb001b, #f79e1b);
  box-shadow: 0 4px 15px rgba(235, 0, 27, 0.3);
}

.mastercard-logo {
  position: relative;
  width: 40px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.circle {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  position: absolute;
}

.circle-red {
  background: #eb001b;
  left: 0;
}

.circle-orange {
  background: #f79e1b;
  right: 0;
  mix-blend-mode: multiply;
}

/* PAYPAL DESIGN */
.payment-icon.paypal {
  background: linear-gradient(135deg, #003087, #009cde);
  box-shadow: 0 4px 15px rgba(0, 156, 222, 0.3);
}

.paypal-logo {
  position: relative;
  width: 30px;
  height: 24px;
}

.paypal-p1,
.paypal-p2 {
  position: absolute;
  border-radius: 8px 8px 0 8px;
}

.paypal-p1 {
  width: 16px;
  height: 20px;
  background: #009cde;
  left: 0;
  top: 0;
}

.paypal-p2 {
  width: 14px;
  height: 18px;
  background: #012169;
  left: 12px;
  top: 2px;
}

/* STRIPE DESIGN */
.payment-icon.stripe {
  background: linear-gradient(135deg, #635bff, #1a1a2e);
  box-shadow: 0 4px 15px rgba(99, 91, 255, 0.3);
}

.stripe-logo {
  position: relative;
  width: 35px;
  height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2px;
}

.stripe-line {
  height: 3px;
  background: white;
  border-radius: 2px;
  transform-origin: left;
}

.stripe-1 {
  width: 35px;
}

.stripe-2 {
  width: 25px;
  transform: translateX(5px);
}

.stripe-3 {
  width: 30px;
  transform: translateX(2px);
}

/* BITCOIN DESIGN */
.payment-icon.bitcoin {
  background: linear-gradient(135deg, #f7931a, #ff9500);
  box-shadow: 0 4px 15px rgba(247, 147, 26, 0.3);
}

.bitcoin-logo {
  position: relative;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bitcoin-circle {
  position: absolute;
  width: 28px;
  height: 28px;
  border: 3px solid white;
  border-radius: 50%;
  top: 1px;
  left: 1px;
}

.bitcoin-b {
  font-size: 20px;
  font-weight: 700;
  color: white;
  z-index: 2;
  position: relative;
}

/* ETHEREUM DESIGN */
.payment-icon.ethereum {
  background: linear-gradient(135deg, #627eea, #1e2348);
  box-shadow: 0 4px 15px rgba(98, 126, 234, 0.3);
}

.ethereum-logo {
  position: relative;
  width: 24px;
  height: 24px;
}

.ethereum-diamond {
  position: relative;
  width: 100%;
  height: 100%;
}

.eth-top {
  width: 0;
  height: 0;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-bottom: 14px solid #8c94ff;
  position: absolute;
  top: 0;
  left: 0;
}

.eth-bottom {
  width: 0;
  height: 0;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-top: 10px solid #ffffff;
  position: absolute;
  bottom: 0;
  left: 0;
}

/* HOVER ANIMATIONS */
.payment-icon-card:hover .payment-icon {
  transform: scale(1.1) rotate(2deg);
}

.payment-icon-card:hover .visa-chip {
  box-shadow: 0 0 8px rgba(255, 215, 0, 0.8);
}

.payment-icon-card:hover .circle {
  animation: pulseCircle 0.6s ease-in-out;
}

.payment-icon-card:hover .stripe-line {
  animation: slideStripe 0.8s ease-in-out;
}

.payment-icon-card:hover .bitcoin-circle {
  animation: rotateBitcoin 1s ease-in-out;
}

.payment-icon-card:hover .ethereum-diamond {
  animation: glowEth 0.8s ease-in-out;
}

@keyframes pulseCircle {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@keyframes slideStripe {
  0% { transform: translateX(0); }
  50% { transform: translateX(3px); }
  100% { transform: translateX(0); }
}

@keyframes rotateBitcoin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes glowEth {
  0%, 100% { filter: drop-shadow(0 0 0 rgba(140, 148, 255, 0)); }
  50% { filter: drop-shadow(0 0 10px rgba(140, 148, 255, 0.8)); }
}

.payment-label {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-align: center;
  font-family: var(--font-primary);
}

.payment-icon-card:hover .payment-label {
  color: var(--text-primary);
}

/* Print styles */
@media print {
  .modern-nav,
  .burger-menu {
    display: none !important;
  }
}

/* RESPONSIVE DESIGN AND MOBILE COMPATIBILITY */
@media (max-width: 768px) {
  .modern-nav {
    padding: 15px 20px;
  }
  
  .nav-brand .brand-logo {
    font-size: 1.5rem;
  }
  
  .hero-content h1 {
    font-size: 2.5rem;
  }
  
  .services-grid,
  .portfolio-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .bottom-admin-payment-section {
    grid-template-columns: 1fr !important;
    gap: 2rem;
  }
}

/* BOTTOM ADMIN & PAYMENT SECTION LAYOUT */
.bottom-admin-payment-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  padding: 6rem 2rem;
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  border-top: 1px solid rgba(139, 92, 246, 0.1);
  margin-top: 0;
}

.admin-section-bottom,
.payment-section-bottom {
  background: rgba(139, 92, 246, 0.05);
  border: 1px solid rgba(139, 92, 246, 0.2);
  border-radius: 20px;
  padding: 3rem;
  backdrop-filter: blur(10px);
}

.payment-placeholder {
  text-align: center;
  color: #ffffff;
}

.payment-placeholder h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #8b5cf6, #3b82f6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.payment-placeholder p {
  font-size: 1.1rem;
  margin-bottom: 2rem;
  color: rgba(255, 255, 255, 0.8);
}

.payment-methods {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin-top: 2rem;
}

.payment-method {
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  border-radius: 12px;
  padding: 1.5rem 1rem;  
  font-weight: 600;
  color: #8b5cf6;
  transition: all 0.3s ease;
}

.payment-method:hover {
  background: rgba(139, 92, 246, 0.2);
  transform: translateY(-2px);
}